<template>
    <v-menu
    bottom
    >
    <template v-slot:activator="{ on, attrs }">
        <v-btn
        class=""
        v-bind="attrs"
        v-on="on"
        icon
        >
            <v-icon color="black">mdi-translate</v-icon>
        </v-btn>
    </template>
    <v-list>
        <v-list-item
        v-for="(item, i) in items"
        :key="i"
        @click="changeLocale(item.lang)"
        >
        <v-list-item-title>{{ item.title }}</v-list-item-title>
        </v-list-item>
    </v-list>
    </v-menu>

</template>
<script>
import {i18n} from '../../i18n.js'
import EventBus from '../../Helpers/event-bus';
  export default {
    data () {
      return {
        userInfo: localStorage.getItem('user'),
        new_lang:null,
        my_lang:null,
            items: [
            {
                title: 'EN',
                lang:'en',
            },
            {
                title: 'PT',
                lang:'pt',
            },
            ],
        }
    },
    methods: {
        changeLocale(new_lang) {
            if(new_lang != this.my_lang){
                i18n.locale = new_lang;
                this.my_lang=new_lang
                const userData = JSON.parse(this.userInfo);//check user
                if(userData)
                this.setLocale();
            }
        },

        setLocale(){
        this.axios
            .get('userLocale/'+this.my_lang)
            .then();
        },
    },
    created () {
        EventBus.$on("lang",  (data) => {
            //console.log(data)
            this.changeLocale(data.lang)
        });
    },
  }
</script>
